.App {
  position: fixed;
  left: 50%;
  top: 2%;
  width: 10.666667rem;
  height: 8.666667rem;
  margin-left: -5.333333rem;
  margin-right: -4.333333rem;
  font-family: Microsoft YaHei;
  border: 0.066667rem solid black;
  padding: .5rem 2rem;
  background: white;
  font-weight: 900;
  color: black;
  letter-spacing: 0.006667rem;
  line-height: 0.253333rem;
}

.envelope {
  width: 6.666667rem;
  height: 4rem;
  border: 0.04rem solid pink;
  position: fixed;
  left: 35%;
  -webkit-transform: translate(-50%, 0);
  -ms-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  bottom: 30%;
  border-radius: 0.133333rem;
  -webkit-box-shadow: 0.053333rem 0.053333rem 0.026667rem #888888;
  box-shadow: 0.053333rem 0.053333rem 0.026667rem #888888;
  background-color: white;
  font-weight: 900;
}

.triangle-down {
  width: 0;
  height: 0;
  border-left: 3.36rem solid transparent;
  border-right: 3.333333rem solid transparent;
  border-top: 2.133333rem solid pink;
  border-radius: 0.2rem;
  position: absolute;
  left: -0.066667rem;
  top: -0.013333rem;
}

.triangle-up {
  width: 0;
  height: 0;
  border-left: 3.333333rem solid transparent;
  border-right: 3.386667rem solid transparent;
  border-bottom: 2rem solid pink;
  border-radius: 0.2rem;
  position: absolute;
  bottom: -0.013333rem;
  left: -0.053333rem;
}

@-webkit-keyframes pound {
  to {
    -webkit-transform: scale(1.4);
    transform: scale(1.4);
  }
}

@keyframes pound {
  to {
    -webkit-transform: scale(1.4);
    transform: scale(1.4);
  }
}

.heart {
  width: 0.533333rem;
  height: 0.533333rem;
  z-index: 999;
  position: absolute;
  left: 3rem;
  top: 1.733333rem;
  cursor: pointer;
  -webkit-animation: pound .25s infinite alternate;
  animation: pound .25s infinite alternate;
}

.text {
  position: absolute;
  top: -1.333333rem;
  left: 1.333333rem;
  color: blue;
  font-size: 0.32rem;
}

.date {
  position: absolute;
  right: 0.333333rem;
  color: blue;
}

.date-text {
  font-size: 0.32rem;
  color: deeppink;
}


